.lh-1{
	position: relative;
    width: 100%;
	margin-left: auto;
	padding-top: 25px;
	margin-bottom: 55px;
	.contact-section{
		.contact-form{
			width: 100%;
			background-color: #DAE4EA;
			padding: 55px 75px 55px 75px;
			.form-title{
				position: relative;
				text-align: center;
				margin-bottom: 70px;
				h2{
					font-size: 28px;
					font-weight: 700;
					color: #FF9E0C;
				}
				&::before{
					content: '';
					position: absolute;
					bottom: -30%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 115px;
					height: 3px;
					background-color: #FF9E0C;
				}
				@media(max-width: 1025px){
					margin-bottom: 30px;
					h2{
						font-size: 20px;
					}
				}
			}
			form{
				position: relative;
				width: 100%;
				.form-control{
					width: 100%;
					height: 53px;
					margin-bottom: 10px;
					padding-left: 20px;
					border: none;
					border-radius: 5px;
				}
				textarea.form-control{
					height: 145px;
				}
				.button{
					width: 100%;
					text-align: center;
					button.btn-form{
						text-decoration: none;
						background: none;
						color: #545454;
						font-size: 20px;
						font-weight: 700;
						margin-top: 15px;
						border-bottom: solid 3px #FF9E0C;
						border-top: none;
						border-left: none;
						border-right: none;
					}
				}
		
				@media(max-width: 1025px){
					.form-control{
						height: 40px;
					}
					textarea.form-control{
						height: 80px;
					}
				}
			}
			@media(max-width: 1025px){
				padding: 30px;
			}
		}
		.contact-info{
			position: relative;
			width: 100%;
			min-height: 320px;
			float: right;
			background-image: url(..\assets\icons\map-contact.svg);
			background-size: 100% 100%;
			margin-left: auto;
			display: flex;
			align-items: center;
			.desc{
				padding-left: 30px;
				h2{
					font-size: 28px;
					font-weight: 600;
					color: #000;
					margin-bottom: 30px;
				}
				ul{
					padding: 0;
					li{
						list-style: none;
						margin-bottom: 20px;
						line-height: 30px;
						.icon{
							float: left;
						}
						p.info{
							padding-left: 50px;
							font-size: 24px;
							font-weight: 400;
							overflow: hidden;
							max-height: 125px;
						}
					}
				}
				@media(max-width: 1025px){
					h2{
						font-size: 25px;
						margin-bottom: 10px;
					}
					p{
						font-size: 20px !important;
					}
				}
			}
		}
	}
}